import React from 'react';
import * as utils from './utils.js';


//funo que cria cada uma das casas do tabuleiro.
function Square(props) {
    //recebe as props de classe e clique.
    const squareClasses = props['squareClasses'];
    const onClick = props['onClick'];
    //e retorna um "boto" com as propriedades definidas.
    return (
        <button className = { "square " + (squareClasses) } onClick={onClick} />
    );
}

//aqui a classe do tabuleiro que vai tratar de renderizar cada uma das casas.
class Board extends React.Component {
    //recebe como propriedade as coordenadas e a classe e retorna um elemento
    //Square com uma chave, classe e definio de ao no clique.
    renderSquare(coordinates, squareClasses) {
        return (
            <Square
                key = {coordinates}
                squareClasses = {squareClasses}
                onClick = {() => this.props.onClick(coordinates) }
            />
        );
    }
    //no render temos 2 arrays vazios, um para o tabuleiro e outro para colunas.
    render() {
        let boardRender = [];
        let columnsRender = [];
        //os movimentos so armazenados em uma constante que recebe as props de moves.
        const moves = this.props.moves;
        //com um for in vamos trabalhar as coordenadas...
        for (let coordinates in this.props.boardState) {
            //se o boardState no tiver suas coordenadas o cdigo segue.
            //esse hasOwnProperty  um mtodo do React que verifica se aquele objeto tem 
            //a propriedade indicada, no caso coordenadas.
            if (!this.props.boardState.hasOwnProperty(coordinates)) {
                continue;
            }
            //duas const so criadas, uma pra coluna e outra pra linha.
            //esse getColasInt  uma funo do arqtuivo utils.js que 
            //recebe como argumento a coluna e coordenada e retorna columns[coordinate.charAt(0)], 
            //ou seja um coluna com o 1 caractere da coordenada.
            const col = utils.getColAsInt(this.props.columns, coordinates);
            //mesma coisa aqui mas aqui retorna parseInt(coordinate.charAt(1), 10);
            //ou seja faz um parseInt no 2 carcatere da coordenada em uma base decimal.
            const row = utils.getRowAsInt(coordinates);

            //aqui se define o jogador por um booleano: se true  o P1, se false  o p2.
            //esse returnPlayerName l em utils.js retorna playerBool === true ? 'player1' : 'player2';
            const currentPlayer = utils.returnPlayerName(this.props.currentPlayer);

            //para definir a cor ce cada quadradinho se faz da seguinte forma:
            //se tanto coluna quanto linha forem mpares (Math.abs(n % 2) === 1;)  branco. 
            //Se linha e colunas NO forem mapres  preto. 
            const colorClass  = ( (utils.isOdd(col) && utils.isOdd(row)) || (!utils.isOdd(col) && !(utils.isOdd(row)) ) ) ? 'white' : 'black';

            //da tudo  armazenado em um array.
            let squareClasses = [];

            //e nesse array damos um push tanto nas coordenadas quanto nas classes de cor. 
            squareClasses.push(coordinates);
            squareClasses.push(colorClass);

            //da so feitas algumas verificaes: se a pea selecionada for igual s coordenadas
            // feito um push no array squareClasses com a string 'isActive'.
            if (this.props.activePiece === coordinates) {
                squareClasses.push('isActive');
            }

            //se o ndice de coordenadas da const moves for maior que -1 (ou seja, se existir algo l)
            //ele cria uma varivel chamada moveClass e atribui a ela o valor de movable junto do 
            //player e da string '-move'. Tambm faz um push em squareclass com essa varivel moveClass.
            if (moves.indexOf(coordinates) > -1) {
                let moveClass = 'movable ' + currentPlayer + '-move';
                squareClasses.push(moveClass);
            }

            //se tiver no boardState alguma coordenada esse if faz um push no squareClass com 
            //as coordenadas oriundas das props desse player junto da string 'piece'.
            if (this.props.boardState[coordinates] !== null) {
                squareClasses.push(this.props.boardState[coordinates].player + ' piece');

                //e se for Dama ele joga l no array tambm...
                if (this.props.boardState[coordinates].isKing === true ) {
                    squareClasses.push('king');
                }
            }

            //junta tudo que tiver no array tirando os espaos.
            squareClasses = squareClasses.join(' ');
            //AQui dei un console log pra ficar mais claro o funcionamento:
            //O tabuleiro  desenhado da seguinte forma: colunas de "a" at "h" e linhas de 1 at 8.
            //comeamos com o square a1 que fica no canto inferior esquerdo. A8 fica no superior esquerdo.
            //h1 fica no canto inferior direito e assim por diante. 
            //Esse array vai mostrar cada quadradinho, se  branco ou preto, se tem pea em cima de qual 
            //jogador, se a pea "x" est selecionada e qual clula desse tabuleiro  movvel, ou seja, pode
            //receber a pea que est selecionada. 
            //A cada seleo de pea e a cada movimento o array  atualizado.
            //console.log(squareClasses)

            //o clumnsRender foi um array vazio que criamos l no comeo desse cdigo e agora 
            //vamos dar um push nele usando a funo de renderSquare mandando os atributos de coordenada, etc.
            //essa funo est l no incio tbm e  responsvel por renderizar cada square com as informaes certinhas. 
            columnsRender.push(this.renderSquare(coordinates, squareClasses, this.props.boardState[coordinates]));
            
            //esse if verifica se o comprimento de columnsRender  maior ou igual a 8 e se for atribui a
            //esse array o seu valor em ordem inversa.
            if (columnsRender.length >= 8) {
                columnsRender = columnsRender.reverse();
                //o boardRender  outro array criado l no incio e dentro desse if vamos tambm dar um push
                //nele com uma div contendo a classe "board-col" e retornando o valor de columnsRender.
                //no final vamos zerar o columnsRender por atribuir a ele mais uma vez um array vazio.
                boardRender.push(<div key={boardRender.length} className="board-col">{columnsRender}</div>);
                columnsRender = [];
                
            }
        }
        //tudo isso que est no Render vai culminar em um return de boardRender.
        return (boardRender);
    }
}

export default Board;


